<template>
  <div>
    <div style="height: 500px" id="map">
    </div>
    <Button @click="clickBtn">按钮</Button>
  </div>
</template>

<script>
import config from '../../setting/mapConfig'
import arcgisPackage from '../../utils/arcgisTools'
export default {
  name: 'demo',
  methods: {
    async createMap () {
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let Point = await arcgisPackage.Point
      let BaseTileLayer = await arcgisPackage.BaseTileLayer
      let Request = await arcgisPackage.Request
      let geometryEngine = await arcgisPackage.geometryEngine
      let GraphicLayer = await arcgisPackage.GraphicsLayer
      let Graphic = await arcgisPackage.Graphic
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let Polygon = await arcgisPackage.Polygon
      let WebTileLayer = await arcgisPackage.WebTileLayer
      let polySym = {
        type: 'simple-fill', // autocasts as new SimpleFillSymbol()
        color: [140, 140, 222, 0.5],
        outline: {
          color: [0, 0, 0, 0.5],
          width: 2
        }
      }

      this.layerRedLine = new FeatureLayer({
        url: config.serverUrl + '/arcgis/rest/services/qhhx/hongxian/MapServer',
        opacity: 0.7,
        outFields: ['*'],
        popupTemplate: {
          title: '{name}',
          content: [
            {
              type: 'fields',
              fieldInfos: [{
                fieldName: 'STATENAME',
                label: '所属地区'
              }, {
                fieldName: '面积',
                label: '面积（平方米）'
              }]
            }
          ]
        }
      })

      // 天地图影像地图
      let TDT = BaseTileLayer.createSubclass({
        properties: {
          urlTemplate: null
        },
        getTileUrl: function (level, row, col) {
          let url = 'http://t' + col % 8 + '.tianditu.gov.cn/DataServer?T=img_w&tk=12b886b3d0f324bd6032c29503972e7c&x=' + col + '&y=' + row + '&l=' + level
          // var url = "http://t0.tianditu.gov.cn/cva_w/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles&TILEMATRIX="+level+"&TILEROW="+row+"&TILECOL="+col+"&tk=12b886b3d0f324bd6032c29503972e7c";
          return url
        },
        fetchTile: function (level, row, col) {
          let url = this.getTileUrl(level, row, col)
          return Request(url, {
            responseType: 'image'
          })
            .then((response) => {
              let image = response.data
              let width = this.tileInfo.size[0]
              let height = this.tileInfo.size[0]

              let canvas = document.createElement('canvas')
              let context = canvas.getContext('2d')
              canvas.width = width
              canvas.height = height

              context.drawImage(image, 0, 0, width, height)

              return canvas
            })
        }
      })

      let tdtylayer = new TDT()

      let td = new WebTileLayer({
        urlTemplate: 'http://t2.tianditu.gov.cn/img_c/wmts?tk=12b886b3d0f324bd6032c29503972e7c&TILECOL={col}&TILEROW={row}&TILEMATRIX={level}&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=c&FORMAT=tiles&SERVICE=WMTS'
      })
      let customBasemap = new Basemap({
        baseLayers: [],
        title: 'Custom Basemap',
        id: 'myBasemap'
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'map',
        center: config.centerPoint,
        zoom: 7
      })

      let point = new Point({
        type: 'point',
        longitude: 98.880521,
        latitude: 38.762178
      })

      let polyline = {
        type: 'polyline',
        paths: [
          [
            [98.880521, 38.762178],
            [98.990521, 38.772178]
          ]
        ],
        spatialReference: { wkid: 4326 }
      }
      let polygon = new Polygon({
        rings: [
          [
            [98.880521, 38.762178],
            [98.990521, 38.972178],
            [98.790521, 38.672178],
            [98.880521, 38.762178]
          ]
        ],
        spatialReference: { wkid: 4326 }
      })
      let bufferLayer = new GraphicLayer()
      this.buffer = geometryEngine.geodesicBuffer(polygon, 0.5, 'kilometers', false)
      this.buffer = geometryEngine.union([this.buffer, polygon])
      // console.log(this.buffer.rings[1])
      this.buffer.rings.pop()
      // this.buffer.removeRing(this.buffer.rings[1])
      this.graphic = new Graphic({
        geometry: this.buffer,
        symbol: polySym
      })
      bufferLayer.add(this.graphic)
      this.map.add(td)
      this.map.add(this.layerRedLine)
      this.map.add(bufferLayer)
      this.bufferLayer = bufferLayer

      this.view.on('pointer-move', evt => {
        point = this.view.toMap({ x: evt.x, y: evt.y })
        /* console.log(mp.x + '-' + mp.y) */
      })
    },
    async clickBtn () {
      let Geoprocessor = await arcgisPackage.Geoprocessor
      let FeatureSet = await arcgisPackage.FeatureSet
      let ImageParameters = await arcgisPackage.ImageParameters
      let Graphic = await arcgisPackage.Graphic
      let GraphicLayer = await arcgisPackage.GraphicsLayer
      let geometryEngine = await arcgisPackage.geometryEngine
      // let gp = new Geoprocessor('http://192.168.1.158:6080/arcgis/rest/services/qhhx/intersect/GPServer/intersect')
      let gp = new Geoprocessor('http://192.168.1.158:6080/arcgis/rest/services/qhhx/insect/GPServer/insect')

      let featureSet = new FeatureSet()

      featureSet.features = [this.graphic]
      /* let params = {
        polygon: featureSet,
        redline: 'wgs'
      } */

      let params = {
        polygon: featureSet,
        redline: 'redline_Project'
      }
      console.log(featureSet)
      gp.submitJob(params).then(result => {
        if (result.jobStatus === 'job-succeeded') {
          gp.getResultData(result.jobId, 'out').then(res => {
            console.log(res)
            let polySym = {
              type: 'simple-fill', // autocasts as new SimpleFillSymbol()
              color: [140, 10, 2, 0.8],
              outline: {
                color: [0, 0, 0, 0.8],
                width: 2
              }
            }
            let graphicArr = res.value.features.map(feature => {
              feature.symbol = polySym
              // 计算面积
              feature.attributes.shapearea = geometryEngine.geodesicArea(feature.geometry, 'square-meters')
              return feature
            })
            let insercLayer = new GraphicLayer()
            insercLayer.addMany(graphicArr)
            this.map.add(insercLayer)
          })
        }
      })
    }
  },
  mounted () {
    this.createMap()
  }
}
</script>

